import React, { useCallback, useState } from "react";
import Button from "../../components/Button";
import { InputGroup } from "../../components/form/InputGroup";
const columns = [
  {
    field: "bankName",
    label: "Bank Name",
  },
  {
    field: "accountName",
    label: "Account Holder Name",
  },
  {
    field: "accountNumber",
    label: "Account Number",
    type: "number",
  },
  {
    field: "ifscCode",
    label: "IFSC Code",
  },
];

export const BankDetailsForm = ({
  step,
  members,
  prevStep,
  setValue,
  onSubmit,
  value,
  state,
}) => {
  const [errors, setErrors] = useState({});
  const onClick = () => {
    let error = false;
    columns.map((column) => {
      const { field } = column;
      if (!state[field] || (state[field] && state[field].length < 1)) {
        error = true;
        setErrors((errors) => {
          return {
            ...errors,
            [field]: "Please Fill this field",
          };
        });
      } else {
        setErrors((errors) => {
          return {
            ...errors,
            [field]: void 0,
          };
        });
      }
    });
    if (!error) {
      setErrors({});
      return onSubmit();
    }
  };
  return (
    <div>
      <h4 className="mb-20">Please enter Bank Details</h4>
      {columns.map((column) => {
        const { label, field } = column;
        return (
          <React.Fragment>
            <InputGroup
              label={label}
              error={[errors[field]]}
              attrs={{
                onChange: (e) => {
                  setValue({ [field]: e.target.value });
                },
                onBlur: (e) => {
                  if (
                    !state[field] ||
                    (state[field] && state[field].length < 1)
                  ) {
                    setErrors((errors) => {
                      return {
                        ...errors,
                        [field]: "Please Fill this field",
                      };
                    });
                  } else {
                    setErrors((errors) => {
                      return {
                        ...errors,
                        [field]: void 0,
                      };
                    });
                  }
                },
                value: state[field],
              }}
            />
          </React.Fragment>
        );
      })}

      <Button
        attrs={{
          //   ref: this.BasicBtn,
          type: "button",
          onClick,
        }}
        options={members}
        size="md"
        className={"mt-5"}
        title={"Continue"}
      />
      <button
        onClick={prevStep}
        className="w-full flex items-center justify-center px-6 py-2 mt-3 border border-transparent text-base rounded-xl text-gray-700 bg-none hover:bg-none active:bg-none focus:outline-none focus:ring-2 focus:ring-primary-200 disabled:text-gray-300 hover:text-primary-500"
      >
        Back
      </button>
    </div>
  );
};
